<template>
  <el-form 
    class="updateLoginPassword" 
    ref="updateLoginPasswordForm" 
    :model="updateLoginPassword" 
    label-width="80px">
    <el-form-item label="旧密码 :">
      <el-input
        size="mini" 
        class="updateLoginPassword__input" 
        v-model="updateLoginPassword.old_password"></el-input>
    </el-form-item>
    <el-form-item label="新密码 :">
      <el-input
        size="mini" 
        class="updateLoginPassword__input" 
        v-model="updateLoginPassword.password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码 :">
      <el-input
        size="mini" 
        class="updateLoginPassword__input" 
        v-model="updateLoginPassword.password_confirmation"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button 
        size="mini" 
        type="primary" 
        @click="onSubmit">立即修改</el-button>
      <el-button size="mini">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { loginPasswordUpdate } from "@/api/common.js";

export default {
  name: "UpdateLoginPassword",
  data() {
    return {
      updateLoginPassword: {
        old_password: "",
        password: "",
        password_confirmation: ""
      }
    }
  },
  methods: {
    onSubmit() {
      loginPasswordUpdate(this.updateLoginPassword).then(() => {
        this.$message.success("修改成功");
        this.updateLoginPassword.old_password = this.updateLoginPassword.password = this.updateLoginPassword.password_confirmation = "";
      }).catch(data => {
        this.$message.error(data.response.data.message);
      })
    }
  }
}
</script>

<style lang="scss">
$prefix: updateLoginPassword;

.#{$prefix} {
  &__input {
    width: 300px;
  }
}
</style>