<template>
  <el-form 
    class="updatePayPassword" 
    ref="updatePayPasswordForm" 
    :model="updatePayPassword" 
    label-width="80px">
    <el-form-item label="旧密码 :">
      <el-input
        size="mini" 
        class="updatePayPassword__input" 
        v-model="updatePayPassword.old_password"></el-input>
    </el-form-item>
    <el-form-item label="新密码 :">
      <el-input
        size="mini" 
        class="updatePayPassword__input" 
        v-model="updatePayPassword.password"></el-input>
    </el-form-item>
    <el-form-item label="确认密码 :">
      <el-input
        size="mini" 
        class="updatePayPassword__input" 
        v-model="updatePayPassword.password_confirmation"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button 
        size="mini" 
        type="primary" 
        @click="onSubmit">立即修改</el-button>
      <el-button size="mini">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { payPasswordUpdate } from "@/api/common.js";

export default {
  name: "UpdatePayPassword",
  data() {
    return {
      updatePayPassword: {
        old_password: "",
        password: "",
        password_confirmation: ""
      }
    }
  },
  methods: {
    onSubmit() {
      payPasswordUpdate(this.updatePayPassword).then(() => {
        this.$message.success("修改成功");
        this.updatePayPassword.old_password = this.updatePayPassword.password = this.updatePayPassword.password_confirmation = "";
      }).catch(data => {
        this.$message.error(data.response.data.message);
      })
    }
  }
}
</script>

<style lang="scss">
$prefix: updatePayPassword;

.#{$prefix} {
  &__input {
    width: 300px;
  }
}
</style>